.game-card {
  background-color: #2c2c2c;
  color: #fff;
  border-radius: 10px;
  padding: 1rem;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.4);
  transition: transform 0.2s, box-shadow 0.2s;
  display: flex;
  flex-direction: column;
  height: 100%;
}

.game-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 0 12px rgba(255, 255, 255, 0.1);
}

.game-card img {
  width: 100%;
  border-radius: 6px;
  margin-bottom: 0.5rem;
  object-fit: cover;
  height: 160px;
}

.game-card a {
  color: #fff;
  text-decoration: none;
}

.game-card a:hover {
  color: #f5c518;
  text-decoration: underline;
}

.game-card h3 {
  color: #fff;
  margin-bottom: 0.5rem;
  font-size: 1.1rem;
}

.game-card .description {
  font-size: 0.85rem;
  color: #ccc;
  margin: 0.3rem 0 0.5rem;
}

.meta-score {
  font-weight: bold;
  padding: 4px 8px;
  border-radius: 4px;
  display: inline-block;
  margin-top: 0.3rem;
  font-size: 0.9rem;
}

.meta-score[data-score="high"] {
  background-color: #6c3;
}
.meta-score[data-score="mid"] {
  background-color: #fc3;
}
.meta-score[data-score="low"] {
  background-color: #e35b5b;
}